import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import movie from '@/page/movie/index'
import wallpaper from '@/page/wallpaper/index'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Wallpaper',
      component: wallpaper
    },
    {
      path: '/wallpaper',
      name: 'Wallpaper',
      component: wallpaper
    },
    {
      path: '/movie',
      name: 'movie',
      component: movie
    }
  ]
})



